@import "./nullstyle.scss";
// ШРИФТУЛИ
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500;600;700&display=swap');

html, body{
	height: 100%;
	line-height: 150%;
	font-size: 16px;
	font-weight: 400;

	scroll-behavior: smooth;

	background: #131313;
}

.body__hidden{
	overflow: hidden;
}

.wrapper{
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.container{
	max-width: 1010px;
	margin: 0 auto;
	padding: 0 15px;
}

.none{
	display: none;
}

.header{
	padding-top: 100px;
}
.header__flex{
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.header__logo{
	padding: 7px;
	border: 1px solid #FFF;

	color:  #FFF;
	font-family: Raleway;
	font-size: 12px;
	font-style: normal;
	font-weight: 500;
	line-height: 112.682%; 
	text-transform: uppercase;	
}
.header__alert{
	span{
		display: inline-block;
		max-width: 147px;
		color: #FFF;
		font-family: Raleway;
		font-size: 12px;
		font-style: normal;
		font-weight: 600;
		line-height: 100%;	

		padding-left: 34px;
		position: relative;

		top: 3.33px;

		&:before{
			content: '';
			display: block;
			width: 24px;
			height: 30px;
			background-image: url('../img/cursor.svg');
			background-position: center;
			background-size: cover;

			position: absolute;
			left: 0;
			top: -3.33px;
		}
	}
}

.pokemons{
	padding: 57px 0 100px;
}
.pokemons__flex{
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.pokemons__list{
	flex-grow: 1;

	display: flex;
	max-width: 484px;
	flex-wrap: wrap;

	margin: -5px -3px;
	padding-right: 15px;
}
.pokemons__item{
	border-radius: 44px;
	padding: 5px 3px;
	span{
		display: block;

		color: #FFF;
		font-family: Raleway;
		font-size: 20px;
		font-style: normal;
		font-weight: 500;
		line-height: 100%;	
		
		background: #1986EC;
		border-radius: 44px;
		padding: 20px;	

		cursor: pointer;
		cursor: url('../img/cursor.svg'), auto;
		transition: 0.2s ease-in-out;
		&:hover{
			background: #3429EF;
			transform: scale(1.05);
		}	
	}
	&.active{
		span{
			background: #08ED9C;
		}
	}
}
.pokemon{
	flex-grow: 1;
	max-width: 484px;

	padding: 44px;
	background: #000;

	min-width: 350px;
}
.pokemon__name{
	color:  #A0A0A0;
	font-family: Raleway;
	font-size: 48px;
	font-weight: 700;
	line-height: 100%;
}
.pokemon__img{
	margin-top: 44px;
	max-width: 396px;
	height: 200px;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
}
.pokemon__img-pic{
	width: 100%;
}
.pokemon__list{
	margin-top: 44px;
	display: flex;
	flex-direction: column;
}
.pokemon__item{
	color:  #A0A0A0;
	font-family: Raleway;
	font-size: 17px;
	font-weight: 500;
	line-height: 150%;
}


@media(max-width:772px){
	.pokemons__flex{
		flex-direction: column-reverse;
		justify-content: center;
		align-items: center;
		padding-right: unset;
	}
	.pokemon{
		width: 100%;
		min-width: unset;
		padding: 30px;
	}
	.pokemon__name{
		font-size: 30px;
	}
	.pokemons__list{
		padding-top: 15px;
		max-width: unset;
		justify-content: center;
		align-items: center;
	}
	.pokemons__item{
		max-width: unset;
		justify-content: center;
	}
	.header{
		padding-top: 30px;
	}
	.pokemons{
		padding-bottom: 30px;
	}
	
}

@media (max-width:600px) {
	.pokemons__list-wrap{
		width: 100%;
		height: 300px;
		align-self: flex-start;
		position: relative;

		overflow-x: auto;
	}
	.pokemons__list{
		position: absolute;
		left: 0;
		top: 0;
		width: 700px;
		justify-content: unset;
	}
}